import { Component, Injector, OnInit } from '@angular/core';
import { BaseForm } from 'src/app/class/base.form';
import { URLz } from 'src/app/enums/url.enum';

@Component({
  selector: 'app-sub-category-add',
  templateUrl: './sub-category-add.component.html',
  styleUrls: ['./sub-category-add.component.css'],
})
export class SubCategoryAddComponent
  extends BaseForm
  implements OnInit
{
  constructor(injector: Injector) {
    super(injector);
    this.param.endpoint = URLz.BASTA_SUB_CAT;
  }
  ngOnInit() {
    this.initForm();
    this._fhs._pathLocation = '/madni_basta/sub_category';
    this._activeId = this._fhs._getURLParam('id');
    if (this._activeId != null && Number(this._activeId) > 0) this.patchData();
  }
  initForm() {
    this._fs._form = this._fb.group({
      title: [
        '',
        this._vs._val('Sub Category', {
          minChar: 4,
          maxChar: 100,
          alpha: 1,
        }),
      ],
      category_id: ['', this._vs._val('Category', { isField: 0 })],
    });
  }
  patchData() {
    this._http
      .get({ ...this.param, resource: this._activeId })
      .subscribe((res: any) => {
        let data = res.data.row;
        this._fs._form.patchValue({
          title: data.title,
          category_id: data.category_id,
        });
      });
  }
}
