import { Component, Injector, OnInit } from '@angular/core';
import { URLz } from 'src/app/enums/url.enum';
import { BaseListClass } from 'src/app/class/base-list-class';
@Component({
  selector: 'app-sub-category-list',
  templateUrl: './sub-category-list.component.html',
  styleUrls: ['./sub-category-list.component.css']
})
export class SubCategoryListComponent extends BaseListClass implements OnInit {
  constructor(injector: Injector) {
    super(injector);
  }
  ngOnInit() {
    this._pathLocation = "/madni_basta/sub_category_add";
    this._component = "Madani Basta Sub Category";
    this.initTables();
  }

  initTables(){
    this._reset(URLz.BASTA_SUB_CAT)
    this._tbls[URLz.BASTA_SUB_CAT].columns = ['id','activate','actions','title', 'category_id'];
    this._tbls[URLz.BASTA_SUB_CAT].endpoint = URLz.BASTA_SUB_CAT;
    this._formCreator(this._tbls[URLz.BASTA_SUB_CAT]);
    this._refresh(URLz.BASTA_SUB_CAT)
  }
}
