import { Component, Injector, OnInit } from '@angular/core';
import { BaseForm } from 'src/app/class/base.form';
import { URLz } from 'src/app/enums/url.enum';

@Component({
  selector: 'di-place-add',
  templateUrl: './place-add.component.html',
  styleUrls: ['./place-add.component.css'],
})
export class PlaceAddComponent extends BaseForm implements OnInit {
  constructor(injector: Injector) {
    super(injector);
    this.param.endpoint = URLz.PLACE;
  }
  ngOnInit() {
    this.initForm();
    this._fhs._pathLocation = '/madni_basta/place';
    this._activeId = this._fhs._getURLParam('id');
    this.patchData();
  }
  initForm() {
    this._fs._form = this._fb.group({
      title: [
        '',
        this._vs._val('Place', { minChar: 4, maxChar: 100, alpha: 1 }),
      ],
    });
  }
  patchData() {
    if (this._activeId != null && Number(this._activeId) > 0){
      this._http
        .get({ ...this.param, resource: this._activeId })
        .subscribe((res: any) => {
          this._fs._form.patchValue({
            title: res.data.row.title,
          });
        });
    }
  }
}
