import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { ACTION } from "src/app/enums/action.enum";
import { CategoryAddComponent } from "./category-add/category-add.component";
import { CategoryListComponent } from "./category-list/category-list.component";
import { EventAddComponent } from "./event-add/event-add.component";
import { EventListComponent } from "./event-list/event-list.component";
import { PlaceAddComponent } from "./place-add/place-add.component";
import { PlaceListComponent } from "./place-list/place-list.component";
import { SubCategoryAddComponent } from "./sub-category-add/sub-category-add.component";
import { SubCategoryListComponent } from "./sub-category-list/sub-category-list.component";

export const routes: Routes = [
  {
    path: "",
    children: [
      {
        path: "category",
        component: CategoryListComponent,
        data: {
          title: "Category List",
          permission: [
            ACTION.VIEW
          ],
          urls: [
            { title: "Madani Basta"},
            { title: "Cateogory" },
            { title: "List" }
          ]
        }
      },
      {
        path: "category_add",
        component: CategoryAddComponent,
        data: {
          title: "Category Add",
          permission: [
            ACTION.ADD,
            ACTION.EDIT
          ],
          urls: [
            { title: "Madani Basta"},
            { title: "Category" },
            { title: "Add" }
          ]
        }
      },
      {
        path: "sub_category",
        component: SubCategoryListComponent,
        data: {
          title: "Sub Category List",
          permission: [
            ACTION.VIEW
          ],
          urls: [
            { title: "Madani Basta"},
            { title: "Sub Category" },
            { title: "List" }
          ]
        }
      },
      {
        path: "sub_category_add",
        component: SubCategoryAddComponent,
        data: {
          title: "Sub Category Add",
          permission: [
            ACTION.ADD,
            ACTION.EDIT
          ],
          urls: [
            { title: "Madani Basta"},
            { title: "Sub Category" },
            { title: "Add" }
          ]
        }
      },
      {
        path: "event",
        component: EventListComponent,
        data: {
          title: "Event List",
          permission: [
            ACTION.VIEW
          ],
          urls: [
            { title: "Madani Basta"},
            { title: "Event" },
            { title: "List" }
          ]
        }
      },
      {
        path: "event_add",
        component: EventAddComponent,

        data: {
          title: "Event Add",
          permission: [
            ACTION.ADD,
            ACTION.EDIT
          ],
          urls: [
            { title: "Madani Basta"},
            { title: "Event" },
            { title: "Add" }
          ]
        }
      },
      {
        path: "place",
        component: PlaceListComponent,
        data: {
          title: "Place List",
          permission: [
            ACTION.VIEW
          ],
          urls: [
            { title: "Madani Basta"},
            { title: "Place" },
            { title: "List" }
          ]
        }
      },
      {
        path: "place_add",
        component: PlaceAddComponent,
        data: {
          title: "Place Add",
          permission: [
            ACTION.ADD,
            ACTION.EDIT
          ],
          urls: [
            { title: "Madani Basta"},
            { title: "Event" },
            { title: "Add" },
          ]
        }
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class MadniBastaRoutingModule { }
