import { Component, Injector, OnInit } from '@angular/core';
import { BaseListClass } from 'src/app/class/base-list-class';
import { URLz } from 'src/app/enums/url.enum';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-category-list',
  templateUrl: './category-list.component.html',
  styleUrls: ['./category-list.component.css']
})
export class CategoryListComponent extends BaseListClass implements OnInit {
  constructor(public injector: Injector) {
    super(injector);
  }
  ngOnInit() {
    this._pathLocation = "/madni_basta/category_add"
    this._component = "Madani Basta Category"
    this.initTables();
  }

  initTables(){
    this._reset(URLz.BASTA_CAT)
    this._tbls[URLz.BASTA_CAT].columns = ['id','activate','actions','title'];
    this._tbls[URLz.BASTA_CAT].endpoint = URLz.BASTA_CAT;
    this._formCreator(this._tbls[URLz.BASTA_CAT]);
    this._refresh(URLz.BASTA_CAT)
  }
}
