import { Component, Injector, OnInit } from '@angular/core';
import { URLz } from 'src/app/enums/url.enum';
import { environment } from 'src/environments/environment';
import { BaseListClass } from 'src/app/class/base-list-class';

@Component({
  selector: 'app-event-list',
  templateUrl: './event-list.component.html',
  styleUrls: ['./event-list.component.css']
})
export class EventListComponent extends BaseListClass implements OnInit {
  constructor(injector: Injector) {
    super(injector);
  }
  ngOnInit() {
    this._pathLocation = "/madni_basta/event_add";
    this._component = "Madani Basta Event"
    this.initTables();
  }

  initTables(){
    this._reset(URLz.EVENT)
    this._tbls[URLz.EVENT].columns = ['id','activate','actions' ,'title'];
    this._tbls[URLz.EVENT].endpoint = URLz.EVENT;
    this._formCreator(this._tbls[URLz.EVENT]);
    this._refresh(URLz.EVENT)
  }
}
