import { Component, Injector, OnInit } from '@angular/core';
import { BaseForm } from 'src/app/class/base.form';
import { URLz } from 'src/app/enums/url.enum';
import { MadniBastaEvent } from 'src/app/interface/madni-basta/madni-basta-event';

@Component({
  selector: 'app-event-add',
  templateUrl: './event-add.component.html',
  styleUrls: ['./event-add.component.css'],
})
export class EventAddComponent extends BaseForm implements OnInit {
  constructor(injector: Injector) {
    super(injector);
    this.param.endpoint = URLz.EVENT;
  }
  ngOnInit() {
    this.initForm();
    this._fhs._pathLocation = '/madni_basta/event';
    this._activeId = this._fhs._getURLParam('id');
    if (this._activeId != null && Number(this._activeId) > 0) this.patchData();
  }
  initForm() {
    this._fs._form = this._fb.group(
      {
        title: [
          '',
          this._vs._val('Event Title', { minChar: 4, maxChar: 100, alpha: 1 }),
        ],
      },
      { updateOn: 'blur' }
    );
  }
  patchData() {
    this._http
      .get({ ...this.param, resource: this._activeId })
      .subscribe((res: any) => {
        this._fs._form.patchValue({
          title: res.data.row.title,
        });
      });
  }
}
