import { Component, Injector, OnInit } from '@angular/core';
import { URLz } from 'src/app/enums/url.enum';
import { environment } from 'src/environments/environment';
import { BaseListClass } from 'src/app/class/base-list-class';

@Component({
  selector: 'app-place-list',
  templateUrl: './place-list.component.html',
  styleUrls: ['./place-list.component.css']
})
export class PlaceListComponent extends BaseListClass implements OnInit {
  constructor(injector: Injector) {
    super(injector);
  }
  ngOnInit() {
    this._pathLocation = "/madni_basta/place_add";
    this._component = "Madani Basta Place";
    this.initTables();
  }
  initTables(){
    this._reset(URLz.PLACE)
    this._tbls[URLz.PLACE].columns = ['id','activate','actions','title'];
    this._tbls[URLz.PLACE].endpoint = URLz.PLACE;
    this._formCreator(this._tbls[URLz.PLACE]);
    this._refresh(URLz.PLACE)
  }
}

