import { Component, Injector, OnInit } from '@angular/core';
import { BaseForm } from 'src/app/class/base.form';
import { URLz } from 'src/app/enums/url.enum';
import { MadniBastaCategory } from 'src/app/interface/madni-basta/madni-basta-category';
@Component({
  selector: 'app-category-add',
  templateUrl: './category-add.component.html',
  styleUrls: ['./category-add.component.css'],
})
export class CategoryAddComponent extends BaseForm implements OnInit {
  constructor(injector: Injector) {
    super(injector);
    this.param.endpoint = URLz.BASTA_CAT;
  }
  ngOnInit() {
    this._fhs._pathLocation = '/madni_basta/category';
    this.initForm();
    this._activeId = this._fhs._getURLParam('id');
    this.patchData();
  }
  initForm() {
    this._fs._form = this._fb.group({
      title: [
        '',
        this._vs._val('Madani Basta Category', {
          minChar: 4,
          maxChar: 100,
          alpha: 1,
        }),
      ],
    });
  }
  patchData() {
    if (this._activeId != null && Number(this._activeId) > 0){
    this._http
      .get({ ...this.param, resource: this._activeId })
      .subscribe((res: any) => {
        const data = res.data.row;
        this._fs._form.patchValue({
          title: data.title,
        });
      });
    }
  }
}
